.navMenu {
  justify-content: space-between;
  vertical-align: middle;
  align-items: center;
  position: sticky;
  transition: 0.5s;
  z-index: 1000;
  top: 0;
  /* border: 2px solid yellow; */
  /* backdrop-filter: blur(1px); */
}

.legendName {
  display: inline;
  vertical-align: middle;
  letter-spacing: 2px;
  /* border: 2px solid yellow; */
  /* text-transform: 'uppercase'; */
}

#legendNameWrapper {
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  position: relative;
  padding: 5px 10px;
  cursor: pointer;
  border: none;
}

#legendNameWrapper::before,
#legendNameWrapper::after {
  content: '';
  width: 100%;
  height: 100%;
  border: none;
  /* border: 3px solid #c01ac0; */
  transition: 0.2s;
  position: absolute;
  border-color: #c01ac0;
}

#legendNameWrapper:hover:before {
  top: 0;
  left: 0;
  width: 10%;
  height: 40%;
  border-top: 3px solid #c01ac0;
  border-left: 3px solid #c01ac0;
}

#legendNameWrapper:hover:after {
  right: 0;
  bottom: 0;
  width: 10%;
  height: 40%;
  border-bottom: 3px solid #c01ac0;
  border-right: 3px solid #c01ac0;
}

#navLinks {
  justify-content: center;
  align-items: center;
  gap: 24px;
}

.navLinkCommon {
  cursor: pointer;
  font-size: 18px;
  font-weight: 700;
  position: relative;
}

.navLinkCommon::after {
  transition: 0.2s ease-in-out;
  background: #c01ac0;
  position: absolute;
  bottom: -5px;
  height: 3px;
  content: '';
  width: 0;
  left: 0;
}

.navLinkCommon:hover:after {
  width: 100%;
}

.miniNavMenu {
  backdrop-filter: blur(10px);
  justify-content: center;
  max-width: fit-content;
  border-radius: 50px;
  align-items: center;
  position: fixed;
  margin: auto;
  z-index: 500;
  padding: 1px;
  bottom: 5vh;
  gap: 2px;
  right: 0;
  left: 0;
}

.miniNavMenu button:hover {
  background: none;
  transform: scale(1.5);
  transition: 0.5s;
}
