.githubCalendarWrapper {
  position: relative;
  border-radius: 10px;
}

.githubCalendarWrapper::before,
.githubCalendarWrapper::after {
  content: '';
  position: absolute;
  left: -10px;
  top: -10px;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
  background: linear-gradient(45deg, #6e0dd0, #c01ac0, #6e0dd0);
  background-size: 400%;
  z-index: -1;
  animation: animate 20s linear infinite;
  filter: blur(25px);
}

.gitCalHeadingWrapper {
  justify-content: center;
  align-items: center;
}

.gitCalHeading {
  display: inline-block;
  border-bottom: 4px solid #c01ac0;
  padding-bottom: 8px;
}

.typedWrapper {
  background: linear-gradient(to left, #b83280, #dd6b20, #dd6b20, #b83280);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 36px;
  font-weight: 600;
  font-family: cursive;
}

.typedCursor {
  font-size: 36px;
  animation: 1s blink step-end infinite;
}

@media all and (max-width: 352px) {
  .typedWrapper,
  .typedCursor {
    font-size: 10px;
  }
}

@media all and (max-width: 480px) {
  .githubCalendarWrapper::before,
  .githubCalendarWrapper::after {
    filter: blur(10px);
  }

  .gitCalHeading {
    border-bottom: 3px solid #c01ac0;
    padding-bottom: 5px;
  }

  .typedWrapper,
  .typedCursor {
    font-size: 16px;
  }
}

@media all and (min-width: 481px) and (max-width: 768px) {
  .githubCalendarWrapper::before,
  .githubCalendarWrapper::after {
    filter: blur(20px);
  }

  .typedWrapper,
  .typedCursor {
    font-size: 20px;
  }
}

@media all and (min-width: 769px) and (max-width: 991px) {
  .typedWrapper,
  .typedCursor {
    font-size: 22px;
  }
}

@media all and (min-width: 992px) and (max-width: 1280px) {
  .typedWrapper,
  .typedCursor {
    font-size: 24px;
  }
}

@keyframes animate {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 400% 0;
  }
}

@keyframes blink {
  from,
  to {
    color: transparent;
  }
  50% {
    color: #dd6b20;
  }
}
